/* Border radius */
@mixin border-radius($radius) {
    -webkit-border-radius: $radius;
       -moz-border-radius: $radius;
        -ms-border-radius: $radius;
            border-radius: $radius;
}

/* Container */
.container {
    max-width: 1080px;
    width: 100%;
    margin: 0 auto;
}

// Small container
.container--small {
    max-width: 800px;
    width: 100%;
    margin: 0 auto;
}


/* CSS Grid Align */
.align-start { @include align(start); }
.align-center { @include align(center); }
.align-end { @include align(end); }

/* CSS Grid Justify */
.justify-start { @include justify(start); }
.justify-center { @include justify(center); }
.justify-end { @include justify(end); }

/* Small border */
.border-small { 
    border: 1px solid #ccc;
}

/* Medium border */
.border-medium { 
    border: 3px solid #ccc;
}

/* Hide on desktop */
.desktop-hide {
    @include tablet-up {
        display: none;
    }

    @include mobile-only {
        display: block;
    }
}

/* Hide on mobile */
.mobile-hide {
    @include tablet-up {
        display: block;
    }

    @include mobile-only {
        display: none;
    }
}

/* Text align left */
.text-left {
    text-align: left;
}

/* Text align center */
.text-center {
    text-align: center;
}

/* Text align right */
.text-right {
    text-align: right;
}


/* Color white */
.color-white {
    color: $color-white;
}

/* Height small */
.height-small {
    height: 150px;
}

/* Max width small */
.max-width-small {
    max-width: 250px;
}

/* Max width medium */
.max-width--medium {
    max-width: 350px;
}

/* Box shadow */
.box-shadow {
    box-shadow: 0 0.25rem 0.75rem rgba(0, 0, 0, .05)
}

@mixin padding--small {
    @include desktop-up {
        padding: $size-4 $size-6;
    }

    @include tablet-only {
        padding: $size-4 $size-4;
    }

    @include mobile-only {
        padding: $size-4 $size-6;
    }
}

.padding--small { 
    @include padding--small;
}


@mixin padding--medium {
    @include desktop-up {
        padding: $size-3 $size-6;
    }

    @include tablet-only {
        padding: $size-3 $size-4;
    }

    @include mobile-only {
        padding: $size-3 $size-6;
    }
}

.padding--medium { 
    @include padding--medium;
}


@mixin padding--large {
    @include desktop-up {
        padding: $size-2 $size-6;
    }

    @include tablet-only {
        padding: $size-2 $size-4;
    }

    @include mobile-only {
        padding: $size-2 $size-6;
    }
}

.padding--large { 
    @include padding--large;
}

@mixin padding-top--small {
    padding-top: $size-8;
}

.padding-top--small {
    @include padding-top--small;
}


@mixin padding-top--medium {
    padding-top: $size-6;
}

.padding-top--medium {
    @include padding-top--medium;
}


@mixin padding-top--large {
    padding-top: $size-4;
}

.padding-top--large {
    @include padding-top--large;
}


.padding-top--xlarge {
    padding-top: $size-2;
}


.padding-bottom--xsmall {
    padding-bottom: $size-10;
}

.padding-bottom--small {
    padding-bottom: $size-8;
}

.padding-bottom--medium {
    padding-bottom: $size-6;
}

.padding-bottom--large {
    padding-bottom: $size-4;
}

.padding-bottom--xlarge {
    padding-bottom: $size-2;
}


